 .card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  float: right;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}
.card > .img-container > img {
  width: 100%;
  height: 100%
}


.mainBox {
    margin-top: 80px;
    width:100%;
    height:100%;
}

.center {
    text-align: center;
    margin-top: 10px;
}

.image {
  height: 200px;
  width: 200px;
}

